<template>
  <div class="flex">
    <!-- Sidebar com filtros -->
    <aside class="w-64 bg-white shadow p-4 h-screen overflow-y-auto hidden md:block">
      <h2 class="text-lg font-semibold mb-4">Filtrar Pinos</h2>
      <div class="space-y-3">
        <div>
          <label class="block text-sm font-medium">Categoria:</label>
          <select v-model="categoriaSelecionada" class="w-full border rounded px-2 py-1">
            <option value="">Todas</option>
            <option value="Social">üßë‚Äçü§ù‚Äçüßë Social</option>
            <option value="Estudo">üìö Estudo</option>
            <option value="Trabalho">üßë‚Äçüíº Trabalho</option>
          </select>
        </div>
        <div>
          <label class="block text-sm font-medium">Dist√¢ncia:</label>
          <select v-model="filtroDistancia" class="w-full border rounded px-2 py-1">
            <option value="">Todas</option>
            <option value="10">At√© 10km</option>
            <option value="50">At√© 50km</option>
            <option value="100">At√© 100km</option>
          </select>
        </div>
        <div>
          <label class="block text-sm font-medium">Popularidade:</label>
          <select v-model="filtroPopularidade" class="w-full border rounded px-2 py-1">
            <option value="">Todas</option>
            <option value="popular">Mais populares</option>
          </select>
        </div>
      </div>
    </aside>

    <!-- Mapa principal -->
    <div class="flex-1 relative">
      <div id="map" style="width: 100%; height: 100vh;"></div>

      <!-- Card flutuante ao clicar no pino -->
      <div v-if="pinoSelecionado" class="absolute bottom-6 left-1/2 transform -translate-x-1/2 bg-white shadow-lg rounded-xl p-4 w-[300px] z-10">
        <div class="flex justify-between items-center mb-2">
          <h3 class="text-lg font-semibold">{{ pinoSelecionado.nome }}</h3>
          <button @click="pinoSelecionado = null" class="text-gray-500 hover:text-red-600">‚úï</button>
        </div>
        <p class="text-sm">üìç {{ pinoSelecionado.categoria }}</p>
        <p class="text-sm">üë• {{ Object.keys(pinoSelecionado.participantes || {}).length }} participantes</p>
        <button class="mt-3 w-full bg-blue-600 text-white py-1.5 rounded hover:bg-blue-700">
          Entrar no grupo
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { getDatabase, ref, onValue } from 'firebase/database'

export default {
  data() {
    return {
      pinoSelecionado: null,
      categoriaSelecionada: '',
      filtroDistancia: '',
      filtroPopularidade: '',
      minhaPos: { lat: -14.2, lng: -51.9 },
      map: null
    }
  },
  mounted() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(pos => {
        this.minhaPos = {
          lat: pos.coords.latitude,
          lng: pos.coords.longitude
        }
        this.initMap()
      }, this.initMap)
    } else {
      this.initMap()
    }
  },
  methods: {
    getIconForCategoria(cat) {
      if (cat === 'Social') return 'https://maps.google.com/mapfiles/ms/icons/red-dot.png'
      if (cat === 'Estudo') return 'https://maps.google.com/mapfiles/ms/icons/blue-dot.png'
      if (cat === 'Trabalho') return 'https://maps.google.com/mapfiles/ms/icons/green-dot.png'
      return null
    },
    initMap() {
      this.map = new google.maps.Map(document.getElementById("map"), {
        zoom: 5,
        center: this.minhaPos
      })

      const db = getDatabase()
      const pinosRef = ref(db, 'pinos')

      onValue(pinosRef, snapshot => {
        const data = snapshot.val()
        if (!data) return

        Object.entries(data).forEach(([key, pino]) => {
          if (this.categoriaSelecionada && pino.categoria !== this.categoriaSelecionada) return
          if (this.filtroPopularidade === 'popular' && (!pino.participantes || Object.keys(pino.participantes).length < 3)) return

          if (this.filtroDistancia) {
            const distancia = this.calcularDistancia(this.minhaPos.lat, this.minhaPos.lng, pino.lat, pino.lng)
            if (distancia > parseInt(this.filtroDistancia)) return
          }

          const marker = new google.maps.Marker({
            position: { lat: pino.lat, lng: pino.lng },
            map: this.map,
            title: pino.nome,
            icon: this.getIconForCategoria(pino.categoria)
          })

          const infoWindow = new google.maps.InfoWindow({ content: `<b>${pino.nome}</b>` })
          marker.addListener("mouseover", () => infoWindow.open(this.map, marker))
          marker.addListener("mouseout", () => infoWindow.close())
          marker.addListener("click", () => {
            this.pinoSelecionado = pino
          })
        })
      })
    },
    calcularDistancia(lat1, lon1, lat2, lon2) {
      const R = 6371
      const dLat = (lat2 - lat1) * Math.PI / 180
      const dLon = (lon2 - lon1) * Math.PI / 180
      const a =
        Math.sin(dLat / 2) ** 2 +
        Math.cos(lat1 * Math.PI / 180) *
        Math.cos(lat2 * Math.PI / 180) *
        Math.sin(dLon / 2) ** 2
      const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a))
      return R * c
    }
  }
}
</script>
