<template>
  <div>
    <h1>GroupBy</h1>
    <button @click="logout">Logout</button>
    <NovoPino />
    <MapView />
  </div>
</template>

<script>
import MapView from '../components/MapView.vue'
import NovoPino from '../components/NovoPino.vue'
import { getAuth, signOut } from 'firebase/auth'

export default {
  components: { MapView },
  setup() {
    const logout = () => {
      const auth = getAuth()
      signOut(auth)
    }
    return { logout }
  }
}
</script>
